import {Component, OnInit, Input} from '@angular/core';
import {Task} from '../ITasks';
import {TaskService} from '../task.service';
// @ts-ignore
import {ActivatedRoute} from '@angular/router';
import { faDumbbell, faCut, faDog, IconName, IconPrefix } from '@fortawesome/free-solid-svg-icons';
import { FaIconLibrary } from '@fortawesome/angular-fontawesome';
import { findIconDefinition, Icon, library } from '@fortawesome/fontawesome-svg-core';
import { icon } from '@fortawesome/fontawesome-svg-core';
import { GeocodingService } from '../geocoding.service';



@Component({
  selector: 'app-modal-popup',
  templateUrl: './modal-popup.component.html',
  styleUrls: ['./modal-popup.component.css']
})
export class ModalPopupComponent implements OnInit {

  @Input() id!: number;

  task!: Task;

  loaded: boolean = false;

  // constructor(private route: ActivatedRoute, private taskService: TaskService) {
  // }
  path: string = '';

  icon : any = {
    url: this.path,
    scaledSize: {
      width: 40,
      height: 40
    }
}
  i : any;
  //to nasze
  iconPrefix: IconPrefix = 'fas';
  lat!: number;
  long!: number;
  coordinates: any;
  constructor(private route: ActivatedRoute, private taskService: TaskService, library: FaIconLibrary, private geocodingService: GeocodingService) {
   library.addIcons(faDumbbell, faCut, faDog);
  }


  ngOnInit(): void {
    // let id = +this.route.snapshot.paramMap.get('id')!;
    this.getTaskById(this.id)
    // const dumbbell = findIconDefinition({prefix: 'fas', iconName: 'dumbbell'})
    // this.i = icon(dumbbell);
  }

  getTaskById(id: number): void {
    this.taskService.getSpecialistById(id)
      .subscribe((task: Task) => this.task = task
      ).add(() => {
        this.geocodingService.getLatLong(this.task.address).subscribe(data => {
          this.coordinates = data;
          this.lat = this.coordinates['results'][0]['geometry']['location']['lat']
          this.long = this.coordinates['results'][0]['geometry']['location']['lng']
          this.path = this.task.category_path;
          this.loaded = true;
        })
      });
  }

}
